import Axios from "axios";
import Cookies from "universal-cookie";
import React from "react";
import LoadingSpinner from "./LoadingSpinner.js";
import { getStorage, ref, deleteObject } from "firebase/storage";
import { storage } from "./form-components/firebase.js";
import { AnnouncementCard } from "./AnnouncementCard";
import { useNavigate, useLocation } from "react-router-dom";
import {
  StyledMyAnnouncementsContainer,
  StyledMyAnnouncementWrapper,
  StyledBtnWrapper,
  StyledIcon,
} from "../styles/MyAnnouncements.styled";
const cookies = new Cookies();

export default function MyAnnouncements({ prop }) {
  const [isFetchingData, setIsFetchingData] = React.useState(true);
  const [myAnnouncements, setMyAnnouncements] = React.useState([]);
  const userId = prop;
  const navigate = new useNavigate();

  function editAnnouncement(announcement) {
    navigate("/edytuj-ogloszenie", {
      state: {
        announcementId: announcement.announcementId,
        author: announcement.author,
        userId: userId,
        phoneNumber: announcement.phone,
        subject: announcement.subject,
        price: JSON.parse(announcement.price),
        isSingleCostValue:
          JSON.parse(announcement.price).length === 1 ? true : false,
        learningModeValues: JSON.parse(announcement.learningMode),
        scopesValues: JSON.parse(announcement.scope),
        locationArray: JSON.parse(announcement.location),
        shortDescription: announcement.shortDescription,
        description: announcement.description,
        imageName: announcement.image,
      },
    });
  }

  function unfoldAnnoucement(announcement) {
    localStorage.setItem("unfoldedAnnoucement", JSON.stringify(announcement));
    localStorage.setItem("currentLocation", "");
    localStorage.setItem("mapZoom", 17);
    const coord = JSON.parse(announcement.location)[0].coordinates;
    localStorage.setItem("coord", JSON.stringify([coord[1], coord[0]]));
    navigate(`/offers/${announcement.author}`);
  }

  function deleteAnnouncement(announcementId, image) {
    Axios.post(`https://smart-edukacja.onrender.com/delete`, {
      announcementId: announcementId,
    })
      .then((res) => {
        const desertRef = ref(storage, `images/${image}`);
        deleteObject(desertRef)
          .then(() => {
            getMyAnnouncements();
            // File deleted successfully
          })
          .catch((error) => {
            // Uh-oh, an error occurred!
          });
      })
      .catch((error) => {
        error = new Error();
      });
  }

  function getMyAnnouncements() {
    const token = cookies.get("TOKEN");
    setIsFetchingData(true);
    Axios.get(
      `https://smart-edukacja.onrender.com/moje-ogloszenia?userId=${userId}`,
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    )
      .then((res) => {
        setMyAnnouncements(res.data);
        setIsFetchingData(false);
      })
      .catch((error) => {
        setMyAnnouncements([]);
        setIsFetchingData(false);
        error = new Error();
      });
  }

  React.useEffect(() => {
    getMyAnnouncements();
  }, [userId]);

  return (
    <StyledMyAnnouncementsContainer>
      {myAnnouncements.length > 0
        ? myAnnouncements.map((announcement) => {
            return (
              <StyledMyAnnouncementWrapper>
                {/* <StyledLink key={announcement.author_id} to={`/offers`}> */}
                <div
                  onClick={() => unfoldAnnoucement(announcement)}
                  style={{ cursor: "pointer" }}
                >
                  <AnnouncementCard announcement={announcement} />
                </div>
                {/* </StyledLink> */}
                <StyledBtnWrapper>
                  <StyledIcon onClick={() => editAnnouncement(announcement)}>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="20"
                      height="20"
                      fill="currentColor"
                      class="bi bi-pencil-square"
                      viewBox="0 0 16 16"
                    >
                      <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z" />
                      <path
                        fill-rule="evenodd"
                        d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"
                      />
                    </svg>
                  </StyledIcon>
                  <StyledIcon
                    onClick={() =>
                      deleteAnnouncement(
                        announcement.announcementId,
                        announcement.image
                      )
                    }
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="20"
                      height="20"
                      fill="red"
                      class="bi bi-trash3"
                      viewBox="0 0 16 16"
                    >
                      <path d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z" />
                    </svg>
                  </StyledIcon>
                </StyledBtnWrapper>
              </StyledMyAnnouncementWrapper>
            );
          })
        : isFetchingData && <LoadingSpinner />}
    </StyledMyAnnouncementsContainer>
  );
}
